html {
	height: 100%;
	background: url("assets/images/background.png") no-repeat #666666;
	background-size: cover;
}
body {
	font-family: "Montserrat", sans-serif;
	margin: 0;
	height: 100%;
	/* The background from the default site: */

	background-color: rgba(68, 68, 68, 0.5);
	background-image: linear-gradient(
		140deg,
		rgba(68, 68, 68, 0.6) 20%,
		rgba(0, 0, 0, 0.8)
	);
}

#root {
	height: 100%;
}
